@import url('https://fonts.googleapis.com/css2?family=Inter:wght@500&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');

$basic-font-size: 14px;
$border-info-btn: 1px dashed #77A193;
$border-radius: 10px;

$container-size: 610px;
$center-position: 0 auto;

$gradient: linear-gradient(0deg, #EEF0F4, #EEF0F4), #FFFFFF;

$flex: flex;
$font-family: 'Inter', sans-serif;

$link-color: #007bff;
$line-height: 163%;

$none: none;

$time-font-family: 'Roboto', sans-serif;


$comments-container-size: 480px;

.MuiSvgIcon-root {
 margin-right: 10px;
}


// information
.info-page__container {
 width: $container-size;
 max-width: $container-size;
 margin:$center-position;

}

.info-page__paragraph {
 font-family: $font-family;
 font-size: $basic-font-size;
 line-height: $line-height;
}

.info-page__link {
 text-decoration: $none;
 color: $link-color;
}

.info-page__btn {
 width: 100%;
 height: 65px;
 display: $flex !important;
 background: $gradient;
 border: $border-info-btn;
 border-radius: $border-radius;
 box-sizing: border-box;
 align-items: center;
}
.info-page__btn__plus {
 font-size: 35px !important;
}
// comments
.comment-list__container {
 width: $comments-container-size;
 max-width: $comments-container-size;
 margin:$center-position;
}

.comment-list__icon {
  margin-right: 60px;
  margin-top: 5px;
}
.comment-list__header {
 font-family: $font-family;
 display: $flex;
 justify-content: space-between;
 margin-bottom: 20px;
}

.comment-list__title {
 font-family: $font-family;
 font-weight: bold;
 font-size: 16px;
 line-height: 19px;
}

.comment-list__body {
  background: #fff;
 height: 462px;
 max-height: 462px;
 overflow: scroll;
 overflow-x: hidden
 ;
}

.comment-list__input-futter {
  background: #EEF0F4;
 }
.comment-list__input-futter {
 width: 100%;
 max-width: 480px;
 display: $flex;
 justify-content: center;
 padding:  20px 0px 20px 0px;

}
 .comment-list__textarea {
  padding-top: 12px;
  width: 420px;
  height: 72px;
  padding-left: 15px;
  border-radius: $border-radius;
  border: $none;
 }
 input::placeholder {
  font-family: $font-family;
  font-size:12px;
 }

 .comment-list__input-image {
   position: absolute;
   right: 24%;
   padding-top: 12px;
 }

 .img {
  width: 48px;
  height: 48px;
 }
 
 .user-messages {
  display: $flex ;
  justify-content: space-between;
  margin-bottom: 20px;
  width: 420px;
 }
.user-messages__avatar {
 display: $flex;
}

.user-messages__img {
 margin-right: 10px;
}
.user-messages__text p{
  margin-top: 0;
  margin-bottom: 0;
  line-height: 22px;
}
.user-messages__text {
 font-family: $font-family;
 font-size: 13px;
}

.user-messages__sending-time__span {
  font-size: 13px;

 font-family: $time-font-family;
 line-height: 21px;
 color: #8A93AD;
}
